import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"

const IndexPage = () => (
  <Layout>
    <SEO title="Home" />
    <article>
        <img className={"person_photo"} src={'../../varnakova/varnakova_3.jpg'} alt={""} />
        <div>
          <div className={"person_name"}>Ольга Варнакова</div>
        </div>
      <div className={"textBlock"}>
          <p>В&nbsp;проведении времени дома есть свои плюсы и&nbsp;минусы. С&nbsp;одной стороны, ты&nbsp;можешь практиковаться в&nbsp;хобби, на&nbsp;которые не&nbsp;было времени раньше; а&nbsp;с&nbsp;другой, ты&nbsp;не&nbsp;можешь видеться с&nbsp;друзьями, не&nbsp;можешь выбраться за&nbsp;рамки собственной квартиры, и&nbsp;это очень сильно мне не&nbsp;нравится.</p>
          <p>Я&nbsp;стала лучше играть на&nbsp;гитаре. Каждый день я&nbsp;уделяю 1&ndash;2 часа практике в&nbsp;игре. Благодаря карантину у&nbsp;меня появилось намного больше времени, чтобы заняться любимым делом.</p>
          <p>За&nbsp;время карантина я&nbsp;посмотрела огромное количество фильмов. Буквально неделю назад я&nbsp;смотрела фильмы каждый день и&nbsp;даже писала свои отзывы по&nbsp;ним в&nbsp;своём дневнике. Что&nbsp;же могу порекомендовать? Я&nbsp;смотрю очень разное кино, смотря по&nbsp;настрою, но&nbsp;исходя из&nbsp;последнего могу посоветовать фильм &laquo;Достать ножи&raquo;, очень интересный детективный сюжет, непредсказуемый поворот событий&hellip; В&nbsp;общем, если вы&nbsp;увлекаетесь просмотром такого рода фильмов, то&nbsp;посмотрите.</p>
          <p>С&nbsp;моей семьей мы&nbsp;стали больше времени проводить вместе. Иногда смотрим вместе <nobr>какие-то</nobr> фильмы, но&nbsp;это уже давно было традицией нашей семьи, нежели <nobr>какой-то</nobr> новинкой. Так что, я&nbsp;думаю, правильней будет сказать, что мы&nbsp;закрепили наши старые традиции в&nbsp;семье.</p>
          <p>Я&nbsp;общаюсь со&nbsp;всеми своими&nbsp;друзьями по&nbsp;видеозвонкам или в&nbsp;переписках, как им&nbsp;бывает удобнее. <nobr>28-ого</nobr> апреля у&nbsp;нашей подруги день рождения, и&nbsp;мы&nbsp;планируем сделать ей&nbsp;<nobr>видео-сюрприз</nobr>. Забавные случаи происходят с&nbsp;нами каждый день, и&nbsp;я&nbsp;не&nbsp;преувеличиваю! К&nbsp;примеру, один из&nbsp;последних: мы&nbsp;с&nbsp;друзьями создали свою музыкальную группу, всё изначально было шуткой, но&nbsp;сейчас мы&nbsp;уже и&nbsp;вправду думаем о&nbsp;создании своего первого трека!</p>
          <p>На&nbsp;наших онлайн уроках тоже бывает очень весело! К&nbsp;примеру, один раз мы&nbsp;с&nbsp;подругой сверяли ответы в&nbsp;задаче на&nbsp;<nobr>онлайн-уроке</nobr>, и&nbsp;когда она записывала аудио, забыла выключить микрофон! Она продолжала записывать, а&nbsp;слышали все! Я&nbsp;старалась ей&nbsp;писать, но&nbsp;сообщения, как назло, не&nbsp;отправлялись! Слава богу, что старый добрый чат в&nbsp;Зуме помог, и, вроде как, <nobr>чьего-то</nobr> недопонимания мы&nbsp;не&nbsp;вызвали. Мне кажется, наши учителя стали снисходительнее, чем во&nbsp;время очного обучения, но&nbsp;по&nbsp;ним в&nbsp;любом формате обучения я&nbsp;очень скучаю!</p>
          <p>Первый день после снятия режима самоизоляции я&nbsp;проведу с&nbsp;друзьями и&nbsp;ещё раз с&nbsp;друзьями! Мы&nbsp;уже договорились с&nbsp;друзьями из&nbsp;нашей компании, что обязательно сходим погулять, и&nbsp;многие согласились! Мы, по&nbsp;предварительным планам, собираемся просто погулять по&nbsp;парку, ведь для нас каждая прогулка&nbsp;&mdash; это целое приключение!</p>
          <p>Очень хочу посетить Францию и&nbsp;США. Франция&nbsp;&mdash; это моя мечта с&nbsp;детства, и&nbsp;я&nbsp;должна была полететь туда ещё в&nbsp;мае, но&hellip;.карантин решил <nobr>по-другому</nobr>, и&nbsp;<nobr>из-за</nobr> этого я&nbsp;очень расстроена.</p>
          <p>Я&nbsp;совершенно не&nbsp;нравлюсь себе в&nbsp;объективе моего девайса. Нет! Вообще нет! Не&nbsp;знаю почему, но&nbsp;на&nbsp;моей <nobr>веб-камере</nobr> очень странная передача цветов: мои щёки всегда красные! На&nbsp;первом занятии я&nbsp;даже хотела их&nbsp;замазать, думая, что это у&nbsp;меня аллергия. Но&nbsp;похоже, аллергия у&nbsp;моего компьютера&hellip;</p>
          <p>Люди не&nbsp;в&nbsp;первый раз проходят через подобного рода заболевания, но&nbsp;в&nbsp;наше время такое масштабное впервые. Наверное, все, как и&nbsp;предполагается, восстановятся, начнут ходить на&nbsp;работу и&nbsp;в&nbsp;учебные заведения (для школьников тут как карта ляжет, возможно вообще летом придётся учиться&hellip;). Что&nbsp;же изменится? Люди задумаются о&nbsp;вопросе гигиены, как это делают сейчас, поймут, к&nbsp;каким рискам её отсутствие может привести, если до&nbsp;сих пор не&nbsp;поняли. И, наверное, начнут дорожить тем местом, в&nbsp;котором они учатся/работают. Я&nbsp;очень полюбила школу, соскучилась по&nbsp;ней за&nbsp;время карантина, так что я&nbsp;даже готова учиться летом! Есть много теорий происхождения коронавируса, тут играет роль не&nbsp;только гигиена, но&nbsp;и&nbsp;экология, социальные связи. Люди уже знают о&nbsp;том, какие риски несёт за&nbsp;собой заражение <nobr>ВИЧ-ем</nobr>, так что, я&nbsp;думаю, люди закрепят эти знания и&nbsp;начнут внимательнее относиться к&nbsp;друг другу и&nbsp;к&nbsp;природе.</p>
          <p>Если представить, что на&nbsp;время карантина мне дадут <nobr>какую-то</nobr> Телепортация! А&nbsp;другого выбора и&nbsp;быть не&nbsp;могло! Если&nbsp;бы у&nbsp;меня была возможность, я&nbsp;бы обязательно телепортировалась к&nbsp;своим друзьям, так&nbsp;бы и&nbsp;карантин проживать было веселее! Ну, конечно&nbsp;же, соблюдая правила гигиены!</p>
      </div>
    </article>
  </Layout>
)

export default IndexPage



